<template>
    <div
        class="controls"
        :class="{
            'disabled': disabled && !isTouchMode,
            'touch-mode': isTouchMode
        }"
    >
        <div
            class="btn btn-normal tool-btn"
            @click="addNode"
        >
            <i class="material-icons">add</i>
        </div>
        <slot></slot>
        <label class="checkbox">
            <input
                type="checkbox"
                ref="checkbox"
                :checked="selected"
                @change="selectEvent"
            />
            <i
                class="checkbox-icon"
                @touchstart="_touchstart"
                @touchend="_touchend"
            ></i>
        </label>
    </div>
</template>

<script>
import getNodeObj from "../mixin/getNodeObj"
import EventBus from "../../common/EventBus"

export default {
    data() {
        return {
            timeout: null
        }
    },
    props: [
        "isTouchMode",
        "disabled", "selected",
        "location", "parentType"
    ],
    inject: ["selectedNode"],
    mixins: [getNodeObj],
    methods: {
        _touchstart() {
            this.timeout = setTimeout(() => {
                this.addNode()
            }, 1000)
        },
        _touchend() {
            clearTimeout(this.timeout)
        },
        selectEvent() {
            //  checked
            const checked = this.$refs.checkbox.checked
            this.getThisObj.SL = checked

            // 
            if (checked) {
                // 
                if (this.selectedNode.loc) {
                    // 
                    this.selectedNode.obj.SL = false
                }

                this.selectedNode.loc = this.location
                this.selectedNode.obj = this.getThisObj
                this.selectedNode.type = this.parentType
                // 
                if (this.parentType == "list") {
                    this.selectedNode.type =
                        (this.$parent.isNested) ?
                            "nestedList" :
                            "notNestedList"
                }

                if (this.parentType == "table") {
                    EventBus.emit("table-selected")
                }
            } else { // 
                this.selectedNode.loc = null
                this.selectedNode.obj = null
                this.selectedNode.type = null
            }
        },

        /* ------------ */

        tableAdder(CTS) {
            // 
            const rowNum = CTS.length
            const colNum = CTS[0].length
            // 
            let newRow = []
            for (let i = 0; i < colNum; i++) {
                newRow.push("")
            }
            // 
            CTS.push(newRow)
            EventBus.emit("add-history", {
                loc: this.location,
                prop: "ROW",
                before: rowNum,
                after: rowNum + 1
            })
        },
        detailAdder(CTS) {
            //    
            CTS.push({
                CT: "",
                CL: "#333",
                SL: false
            })
            // 
            const loc = Object.create(this.location)
            loc.push(CTS.length - 1)
            EventBus.emit("add-history", {
                loc,
                prop: "IST"
            })
        },
        listAdder(CTS) {
            //    
            CTS.push({
                NT: "li",
                CT: "",
                CL: "#333",
                SL: false
            })
            // 
            const loc = Object.create(this.location)
            loc.push(CTS.length - 1)
            EventBus.emit("add-history", {
                loc,
                prop: "IST"
            })
        },
        floorAdder(CTS){
            // 
            EventBus.off("textfield-return-" + this.parentType)
            EventBus.on("textfield-return-" + this.parentType, (obj) => {
                // 
                if (obj) {
                    CTS.push(obj)
                }
                // 
                const loc = this.location
                loc.push(CTS.length - 1)
                EventBus.emit("add-history", {
                    loc,
                    prop: "IST"
                })
                // 
                EventBus.off("textfield-return-" + this.parentType)
            })

            EventBus.emit("textfield-open", this.parentType)
        },
        // 
        addNode() {
            const CTS = this.getThisObj.CTS

            switch (this.parentType) {
                case "table":
                    this.tableAdder(CTS)
                    break
                case "details":
                    this.detailAdder(CTS)
                    break
                case "list":
                    this.listAdder(CTS)
                    break
                case "floor":
                    this.floorAdder(CTS)
            }
        }
    }
}
</script>

<style scoped>
    /* tool Button */
    .tool-btn {
        width: 10%;
        min-width: 60px;
        max-width: 84px;
        height: 32px;
        line-height: 32px;
        margin: 0 40px 0;
        background-color: #CFD8DC;
    }
    /* Controls */
    .controls {
        display: flex;
        height: 32px;
        -webkit-box-sizing: border-box;
            box-sizing: border-box;
        transition: height .3s;
    }
    .controls.disabled {
        height: 0 !important;
        margin: 0;
    }
    
    /* Touch Mode */
    .touch-mode {
        position: absolute;
        top: 0;
        left: -44px;
    }
</style>
<style>
    .controls.disabled * {
        display: none;
    }
    .touch-mode .tool-btn {
        display: none;
    }
</style>