<script>
export default {
    data() {
        return {
            hover: false,
            setTimeout: null,
        }
    },
    methods: {
        //  
        onMouseEnter() {
            //  .4s 
            this.setTimeout = setTimeout(() => {
                this.hover = true
            }, 400)
        },
        // 
        onMouseLeave() {
            // 
            clearTimeout(this.setTimeout)
            if (!this.selected) {
                this.hover = false
            }
        }
    }
}
</script>